<template>
  <div>
    <section class="upload-docs">
      <PageHeading :text="`Document Upload`" />
    </section>
    {{documents.toString()}}
    <div class="container">
      <p>Document Type:</p>
      <select v-model="documentType" @change="(event) => documentType = event.target.value">
        <option value="Transcripts">Transcripts</option>
        <option value="ISIRS">ISIRS</option>
      </select>
      <br />
      <br />
      <p>IRN:</p>
      <input type="text" v-model="irn" />
      <br />
      <br />
      <div class="large-12 medium-12 small-12 cell">
        <label>
          File:
          <input type="file" id="file" ref="file" v-on:change="handleFileUpload()" />
        </label>
        <button v-if="uploadStatus !== 'Pending...'" v-on:click="submitFile()">Submit</button>
      </div>
      <br />
      <p class="message" v-if="uploadStatus !== 'Pending...'">{{uploadStatus}}</p>
    </div>
    <LoadingSpinner v-if="uploadStatus === 'Pending...'" />
  </div>
</template>

<script>
import LoadingSpinner from "../components/UI/LoadingSpinner";
import PageHeading from "../components/UI/PageHeading";
import axios from "axios";

export default {
  name: "UploadDocs",
  components: {
    LoadingSpinner,
    PageHeading,
  },
  data() {
    return {
      isLoading: true,
      documents: [],
      file: "",
      documentType: "Transcripts",
      irn: "",
      uploadStatus: "",
    };
  },
  computed: {
    userName() {
      return this.$store.state.userName;
    },
  },
  methods: {
    handleFileUpload() {
      this.file = this.$refs.file.files[0];
      console.log(file);
    },
    async submitFile() {
      this.uploadStatus = "Pending...";
      let formData = new FormData();
      formData.append("file", this.file);
      console.log(this.irn, this.documentType);
      try {
        const { status } = await axios.post(
          `/api/v1/topcoder/document/upload/${this.documentType}/${this.irn}`,
          formData,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }
        );
        console.log(status)
        this.uploadStatus = "Successful Upload"
      } catch (err) {
        console.log(err)
        this.uploadStatus = "Failed Upload";
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../scss/variables";
h1 {
  color: $secondary;
  font-family: $font-secondary;
}
.upload-docs {
  padding: 10px 5px 5px 5px;
}
.container {
  padding: 10px;
}
.message {
  font-size: 2rem;
}
</style>
